<script>
  import { poemIds, poems, sample, pidsFor, range, getQuery, setQuery, tagLengths, tagDescs } from "./poems";
  import * as qs from 'query-string';
  import { onMount, afterUpdate } from 'svelte';

  let activePoemId = getQuery('p') || poemIds[0];
  let poemWindow;
  let poemColumn;
  let mobileAbbr;
  let lastClickedTag = null;
  let visible = [];
  let observer;
  let showAbout = false;

  $: tagOptions = [['', `all poems (${poemIds.length})`]].concat(tagLengths.map((tl,i) => [tl[1],`${tl[1]} poems (${tl[0]})`]));

  $: allPoemsOrdered = poemIds.map(id => poems[id]);
  $: tagPoemsOrdered = activeTag && activeTagPids.map(id => poems[id]);
  $: poemsOrdered = activeTag ? tagPoemsOrdered : allPoemsOrdered;

  $: activePoem = poems[activePoemId];
  $: prevAbsPoem = activePoem.index > 0 && poems[poemIds[activePoem.index - 1]];
  $: nextAbsPoem = activePoem.index < poemIds.length - 1 && poems[poemIds[activePoem.index + 1]];

  $: activeTag = typeof(lastClickedTag) != 'undefined' && activePoem.tags.indexOf(lastClickedTag) >= 0 && lastClickedTag;
  $: activeTagPids = activeTag && pidsFor(activeTag);
  $: tagIndex = activeTag && activeTagPids.indexOf(activePoem.id);
  $: prevTagPoem = activeTag && tagIndex > 0 && poems[activeTagPids[tagIndex - 1]];
  $: nextTagPoem = activeTag && tagIndex < activeTagPids.length - 1 && poems[activeTagPids[tagIndex + 1]];

  $: prevPoem = activeTag ? prevTagPoem : prevAbsPoem;
  $: nextPoem = activeTag ? nextTagPoem : nextAbsPoem;

  $: poemTitle = activePoem.title.replace(/--/g, "&mdash;");

  let poemEls;
  let updateScroll = undefined;

  afterUpdate(() => {
    if (!activeTag) {
      poemEls = [].slice.call(poemWindow.querySelectorAll('.poem'));
    }
    if (typeof(updateScroll) !== 'undefined') {
      const el = document.getElementById('poem-'+activePoemId);
      poemColumn.scrollTop = el.offsetTop - updateScroll;
      updateScroll = undefined;
    }
  });

  onMount(() => {
    poemEls = [].slice.call(poemWindow.querySelectorAll('.poem'));

    if (activePoemId) {
      const poem = document.getElementById(`poem-${activePoemId}`);
      if (poem) {
        poem.scrollIntoView();
      }
    }

    let prevMiddle = null;

    const onWheel = e => {
      const middle = poemColumn.scrollTop + poemColumn.offsetHeight/2.0;
      let idx, el, top, bot;
      for (let i = 0; i < poemEls.length; i++) {
        el = poemEls[i];
        top = el.offsetTop;
        bot = el.offsetTop + el.offsetHeight;
        if (middle >= top-1 && middle <= bot+1) {
          activePoemId = poemIds[i];
          idx = i;
          break;
        }
      }
      setQuery('p', activePoemId, true);

      const abbr = document.getElementById('tagabbr');
      if (abbr && prevMiddle !== null && prevMiddle != middle) {
        abbr.blur();
      }
      prevMiddle = middle;
    };

    jQuery(poemColumn).scroll((ev) => {
      onWheel(ev);
    });

    setInterval(onWheel, 250);

    if (getQuery('t')) {
      setTimeout(() => setTag(getQuery('t')), 100);
    }
  });

  function scrollNext() {
    if (nextPoem) {
      document.getElementById('poem-'+nextPoem.id).scrollIntoView({
        behavior: 'smooth'
      });
      activePoemId = nextPoem.id;
    }
  }

  function scrollPrev() {
    if (prevPoem) {
      document.getElementById('poem-'+prevPoem.id).scrollIntoView({
        behavior: 'smooth'
      });
      activePoemId = prevPoem.id;
    }
  }

  function scrollNextTagged() {
    if (nextTaggedPoem) {
      const el = document.getElementById('poem-'+nextTaggedPoem.id);
      el.scrollIntoView();
      activePoemId = nextTaggedPoem.id;
    }
  }

  function scrollPrevTagged() {
    if (prevTaggedPoem) {
      const el = document.getElementById('poem-'+prevTaggedPoem.id);
      el.scrollIntoView();
      activePoemId = prevTaggedPoem.id;
    }
  }

  function reorder(event) {
    const q = new URLSearchParams(location.search);
    q.delete('p');
    q.set('ord', event.currentTarget.value);
    location.search = q.toString();
  }

  function gutenTag(tag) {
    if (tag == lastClickedTag) {
      setTag(null);
    } else {
      setTag(tag);
    }
  }

  function retag(event) {
    setTag(event.currentTarget.value);
  }

  function setTag(newTag) {
    if (newTag) {
      const tagPids = pidsFor(newTag);
      if (tagPids.includes(activePoemId)) {
        const el = document.getElementById('poem-'+activePoemId);
        updateScroll = el.offsetTop - poemColumn.scrollTop;
      } else {
        poemColumn.scrollTop = 0;
        activePoemId = tagPids[0];
        updateScroll = undefined;
      }
      lastClickedTag = newTag;
      setQuery('t', newTag, true);
    } else {
      const el = document.getElementById('poem-'+activePoemId);
      updateScroll = el.offsetTop - poemColumn.scrollTop;
      lastClickedTag = null;
      setQuery('t', null, true);
    }
  }

  function toggleAbout() {
    showAbout = !showAbout;
  }
</script>

<style>
  :root {
    --window-size: 550px;
  }
  @media only screen and (max-width: 600px) {
    :root {
      --window-size: min(calc(100vw - 1em), 370px);
    }
  }
  @media only screen and (max-height: 600px) {
    :root {
      --window-size: min(calc(100vh - 8em), 370px);
    }
  }
  @media only screen and (max-width: 600px) and (max-height: 600px) {
    :root {
      --window-size: min(calc(100vw - 1em), calc(100vh - 8em), 370px);
    }
  }

  .about-wrapper {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 3;
    background: rgba(255,255,255,0.99);
    width: 100%;
    height: 100%;
    cursor: pointer;
  }

  .about-inner {
    max-width: var(--window-size);
    //margin: 8rem auto;
    position: absolute;
    top: calc(50% - 4rem);
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
  }

  .poems {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    height: var(--window-size);
    width: var(--window-size);
    overflow: scroll;
    border-radius: calc(var(--window-size) / 2);
    border: 1px solid #ccc;
  }

  .poem-column {
    position: absolute;
    height: var(--window-size);
    width: var(--window-size);
    overflow: scroll;
  }

  .poem {
    padding: 8rem calc(var(--window-size) / 5);
    width: calc(var(--window-size) * 0.6);
    border-bottom: 1px dotted #ccc;
  }

  .poem:first-child {
    padding-top: calc(var(--window-size) / 5);
  }

  .poem:last-child {
    padding-bottom: calc(var(--window-size) / 5);
    border-bottom: none;
  }

  .poem-text {
     padding-left: 2rem;
     text-indent: -2rem;
  }

  .poems,
  .poem-column {
    -ms-overflow-style: none;  /* Internet Explorer 10+ */
    scrollbar-width: none;  /* Firefox */
  }
  .poems::-webkit-scrollbar,
  .poem-column::-webkit-scrollbar {
      display: none;  /* Safari and Chrome */
  }

  .next-poem, .prev-poem {
    height: 1rem;
    position: absolute;
    left: 50%;
    width: calc(var(--window-size) / 2);
    transform: translateX(-50%);
    cursor: pointer;
    padding: 1rem 0;
    text-align: center;
  }

  .next-poem:hover, .prev-poem:hover {
    border: 1px dotted #ccc;
  }

  .carrot {
    position: absolute;
    top: 50%;
    left: 50%;
    font-size: small;
    transform: translateX(-50%) translateY(-50%) scaleX(4);
  }

  .next-poem {
    transform: translateX(-50%) rotateZ(180deg);
    top: calc(50% + var(--window-size) / 2 + 1px);
  }

  .prev-poem {
    bottom: calc(50% + var(--window-size) / 2 + 1px);
  }

  .about-link-wrapper {
    position: absolute;
    max-width: 33%;
    bottom: 5px;
    left: 5px;
  }

  .tags {
    max-width: 33%;
    position: absolute;
    bottom: 5px;
    right: 5px;
  }

  .tag {
    margin-left: 5px;
    display: inline-block;
    float: right;
  }
  .tag.active {
    font-weight: bold;
  }

  .title {
    max-width: 33%;
  }
  h1,h2 {
    margin: 0;
  }

  .order {
    position: absolute;
    right: 5px;
    top: 5px;
  }

  @media only screen and (max-height: 600px) {
    .title {
      width: calc(33% - 8px);
      max-width: 33%;
      text-overflow: ellipsis;
      overflow: hidden;
    }
    .tags {
      width: calc(33% - 8px);
      max-width: 33%;
    }
    .order select {
      display: block;
    }
  }

  @media only screen and (max-width: 600px) {
    h1 {
      font-size: 22px;
      white-space: nowrap;
      width: 100%;
      text-overflow: ellipsis;
      overflow: hidden;
    }
    .title {
      max-width: none;
      width: 100%;
    }
    .order {
      position: initial;
      margin-top: 8px;
    }
    .order select {
      display: inline;
      max-width: calc(50% - 1rem);
    }
    .about-inner {
      width: calc(100% - 2rem);
      padding-left: 2rem;
      padding-right: 2rem;
    }
    .about-link-wrapper {
      bottom: 8px;
      left: initial;
      right: 8px;
    }
    .tags {
      position: absolute;
      bottom: 8px;
      left: 8px;
      width: calc(100% - 24px);
      max-width: 100%;
      float: none;
    }
    .tag {
      float: none;
    }
    .next-poem, .prev-poem {
      padding: 0.5rem 0;
    }
  }

  abbr {
    padding: 0 5px;
    border: 1px dotted #ccc;
    cursor: help;
    text-decoration: none !important;
  }

  @media (pointer: coarse), (hover: none) {
    abbr {
      position: relative;
      display: inline-flex;
      justify-content: center;
      z-index: 2;
    }
    abbr:focus {
      outline: none;
    }
    abbr:focus::after {
      content: attr(title);
      position: absolute;
      top: 100%;
      color: #000;
      background-color: #f8f8f8;
      font-size: 11px;
      font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
      width: fit-content;
      padding-top: 2px;
      padding-bottom: 1px;
      padding-left: 4px;
      padding-right: 4px;
      box-shadow: 0px 0px 1px 1px #ccc;
      z-index: 1;
      outline: none;
    }
  }
</style>

<main>
  <div class='tags'>
    {#each activePoem.tags as tag,i}
      <a class='tag' href='javascript:void(0)' on:click={gutenTag(tag)} title='{tagDescs[tag]}' class:active={tag==activeTag}>#{tag}</a>
    {/each}
  </div>
  <div class='title'>
    <h1>{@html poemTitle}</h1>
  </div>
  <div class='order'>
    <select on:change={reorder}>
      {#each [['rnd','random order'],['chr','oldest first'],['rev','newest first']] as [k,l]}
        <option selected={getQuery('ord') == k} value={k}>{l}</option>
      {/each}
    </select>

    <select on:change={retag}>
      {#each tagOptions as [k,l]}
        <option selected={lastClickedTag === k} value={k}>{l}</option>
      {/each}
    </select>

    {#if activeTag && tagDescs[activeTag]}
      <abbr id="tagabbr" title="{tagDescs[activeTag]}" tabindex="0">?</abbr>
    {/if}
  </div>
  <div class='poems' bind:this={poemWindow}>
    <div class='poem-column poem-column--main' bind:this={poemColumn}>
      {#each poemsOrdered as poem (poem.id)}
        <div class='poem' id='poem-{poem.id}'>
          <div class='poem-text'>
            {@html poem.text}
          </div>
        </div>
      {/each}
    </div>
  </div>
  <div class='about-link-wrapper'>
    <a href='javascript:void(0)' on:click={toggleAbout}>about</a>
  </div>
  {#if prevPoem}
    <div class='prev-poem' on:click={scrollPrev}>
      <span class='carrot'>^</span>
    </div>
  {/if}
  {#if nextPoem}
    <div class='next-poem' on:click={scrollNext}>
      <span class='carrot'>^</span>
    </div>
  {/if}

  {#if showAbout}
    <div class='about-wrapper' on:click={toggleAbout}>
      <div class='about-inner'>
        <h2>Collected Poems</h2>
        <p>This is a collection of my poems from about 2010 to now, arranged in an
        experimental web format.</p>

        <p>To read, scroll inside the circle or click the arrows to skip to the next or previous poem.
        Use the dropdowns to view poems in a different order or filter by thematic tag.</p>

        <p>(Click anywhere to return.)</p>
      </div>
    </div>
  {/if}
</main>
